  padding-top: 13px;
  padding-left: 30px;
  padding-right: 30px;
}

.headerCollapse {
  float: right;
}

Button {
  font-family: 'Do Hyeon', sans-serif;
  float: "right";
  letter-spacing: 3px;
  color: #3b3b3b;
  font-size: 100px;
}

.carousel-caption {
  bottom: 50%;
  font-size: 60px;
  font-weight: 200;
  padding-left: 30px;
  padding-right: 30px;
  letter-spacing: 5px;
  display: inline;
  display: initial;
  background-color: #3b3b3b;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: 200px;
  border-radius: 20px 50px;
}

.carouselButton {
  position: relative;
  right: auto;
  z-index: 10;
  top: -30px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  overflow: hidden;
  color: #fff;
  letter-spacing: 6px;
}


/* Fading animation */
.fade {
  -webkit-animation-name: fade;
  -webkit-animation-duration: 1.5s;
  animation-name: fade;
  animation-duration: 1.5s;
}

@-webkit-keyframes fade {
  from {opacity: .4}
  to {opacity: 1}
}

@keyframes fade {
  from {opacity: .4}
  to {opacity: 1}
}/*Div Attributes*/
div.sticky {
    position: -webkit-sticky;
    position: sticky; top: 200px;
    padding-left: 50px;
  }

.row { /* IE 10 */
  display: -webkit-flex;
  display: flex; /* IE 10 */
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  padding: 0 4px;

}



.column { /* IE 10 */
  -webkit-flex: 50% 1;
          flex: 50% 1;
  padding: 0 4px;
}

.tile img {
  vertical-align: left;
}


.tile {
  -webkit-flex: 20% 1;
          flex: 20% 1;
  background-color: transparent;
  width: 300px;
  height: 300px;
  -webkit-perspective: 1000px;
          perspective: 1000px;
  border-radius: 20px 20px;
}

.tileRow {
  padding: 0;
}

.tileTable{
  padding: 0;
}

.tile-inner {
  position: relative;
  width: 100%;
  height: 100%;
  text-align: center;
  -webkit-transition: -webkit-transform 0.6s;
  transition: -webkit-transform 0.6s;
  transition: transform 0.6s;
  transition: transform 0.6s, -webkit-transform 0.6s;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
  background-color: aquamarine;
  
}

.tile:hover .tile-inner {
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}

.tile-front, .tile-back {
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  
}


.tile-front {
  background-color: #bbb;
  color: black;
  z-index: 2;
  
}

.tile-back {
  background-color: #3b3b3b;
  color: white;
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
  z-index: 1;
}

